  .image-grid-container {
    display: grid;
    align-items: center;
    justify-content: center;
    overflow: scroll;
    height: 300px;
  }
  
  .image-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    grid-auto-rows: minmax(150px, auto);
  }
  
  .image-grid img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    max-width: 300px;
    max-height: 200px;
  }
  